* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px;
}

input {
    font-size: 1.2rem;
    width: 100%;
    color: gray;
}

.card-grid {
    display: flex;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 2px solid gray;
    border-radius: 15px;
    margin: 1rem;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}